"use client";

import { useTranslations } from 'next-intl';
import { HypothesisStageSummary } from "@/lib/hypothesis-data";

interface HypothesisMatrixProps {
  stages: HypothesisStageSummary[];
}

export function HypothesisMatrix({ stages }: HypothesisMatrixProps) {
  const t = useTranslations('hypotheses.matrix');
  const tCommon = useTranslations('common');
  const allHypotheses = stages.flatMap((stage) => stage.items);

  const quadrants = [
    {
      key: "pursue",
      filter: (impact: number, feasibility: number) => impact >= 7 && feasibility >= 7,
      gradient: "green" as const,
    },
    {
      key: "partner",
      filter: (impact: number, feasibility: number) => impact >= 7 && feasibility < 7,
      gradient: "turquoise" as const,
    },
    {
      key: "optimize",
      filter: (impact: number, feasibility: number) => impact < 7 && feasibility >= 7,
      gradient: "turquoise" as const,
    },
    {
      key: "observe",
      filter: (impact: number, feasibility: number) => impact < 7 && feasibility < 7,
      gradient: "turquoise" as const,
    },
  ] as const;

  return (
    <section className="rounded-3xl border border-border/70 bg-surface/80 px-5 py-5">
      <div className="flex flex-col gap-2 md:flex-row md:items-center md:justify-between">
        <div>
          <p className="text-[11px] uppercase tracking-[0.32em] text-text-secondary/60">{t('title')}</p>
          <h3 className="mt-1.5 text-lg font-semibold text-text-primary lg:text-xl">{t('subtitle')}</h3>
        </div>
        <span className="inline-flex items-center gap-2 rounded-full border border-border/60 px-3 py-1.5 text-[11px] uppercase tracking-[0.24em] text-text-secondary">
          {t('displayedInitiatives', { count: allHypotheses.length })}
        </span>
      </div>

      <div className="mt-5 grid grid-cols-1 gap-4 md:grid-cols-2">
        {quadrants.map((quadrant) => {
          const items = allHypotheses.filter((item) => quadrant.filter(item.impact, item.feasibility));
          const gradientClasses = {
            green: "from-[rgba(0,255,136,0.15)] to-[rgba(0,255,136,0.05)] border-[rgba(0,255,136,0.4)]",
            turquoise: "from-[rgba(64,224,208,0.15)] to-[rgba(64,224,208,0.05)] border-[rgba(64,224,208,0.4)]",
          };

          return (
            <div
              key={quadrant.key}
              className={`group relative flex flex-col overflow-hidden rounded-2xl border bg-gradient-to-br p-5 transition duration-300 ${gradientClasses[quadrant.gradient]} hover:shadow-[0_0_28px_rgba(0,255,136,0.25)]`}
            >
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <h4 className="text-base font-semibold text-text-primary">{t(`quadrants.${quadrant.key}.label`)}</h4>
                  <p className="mt-2 text-xs leading-relaxed text-text-secondary">{t(`quadrants.${quadrant.key}.description`)}</p>
                </div>
                <span className="ml-3 text-4xl font-bold text-text-primary">{items.length}</span>
              </div>
              
              {items.length > 0 && (
                <ul className="mt-4 space-y-2">
                  {items.slice(0, 3).map((item) => (
                    <li
                      key={item.id}
                      className="rounded-xl border border-border/60 bg-surface-hover/70 px-3 py-2 text-xs"
                    >
                      <div className="flex items-center justify-between text-[11px] uppercase tracking-wide text-text-secondary/60">
                        <span>{item.id}</span>
                        <span>{item.stage.toLowerCase()}</span>
                      </div>
                      <p className="mt-1.5 text-sm font-semibold text-text-primary leading-tight">{item.title}</p>
                      <p className="mt-1 text-[11px] text-text-secondary">
                        {tCommon('impact')} {item.impact.toFixed(1)} · {tCommon('feasibility')} {item.feasibility.toFixed(1)} · {item.owner}
                      </p>
                    </li>
                  ))}
                  {items.length > 3 && (
                    <li className="pt-1 text-center text-xs text-text-secondary">
                      {t('more', { count: items.length - 3 })}
                    </li>
                  )}
                </ul>
              )}
              
              {items.length === 0 && (
                <div className="mt-4 rounded-xl border border-dashed border-border/60 bg-surface-hover/50 p-6 text-center text-xs text-text-secondary/70">
                  {t('noHypotheses')}
                </div>
              )}
            </div>
          );
        })}
      </div>
    </section>
  );
}

