"use client";

import { useTranslations } from 'next-intl';
import { CalendarClock, ShieldCheck, ExternalLink, CheckCircle2, Sparkles } from "lucide-react";
import { HypothesisDetail } from "@/lib/hypothesis-data";

const checklistTone: Record<HypothesisDetail["gatingChecklist"][number]["status"], string> = {
  complete: "border-[rgba(0,255,136,0.45)] bg-[rgba(0,255,136,0.08)] text-text-primary",
  "in-progress": "border-[rgba(255,180,71,0.45)] bg-[rgba(255,180,71,0.08)] text-text-primary",
  pending: "border-border/60 bg-surface-hover/80 text-text-secondary"
};

interface HypothesisDetailPanelProps {
  detail: HypothesisDetail;
}

export function HypothesisDetailPanel({ detail }: HypothesisDetailPanelProps) {
  const t = useTranslations('hypotheses.detail');

  const metrics = [
    { label: t('impactScore'), value: detail.impact.toFixed(1) },
    { label: t('feasibility'), value: detail.feasibility.toFixed(1) },
    { label: t('confidence'), value: `${Math.round(detail.confidence * 100)}%` },
    { label: t('targetValue'), value: detail.targetValue }
  ];

  return (
    <section className="flex flex-col gap-4 rounded-3xl border border-border/70 bg-surface/80 p-5">
      <header className="flex flex-col gap-3">
        <div className="flex items-start justify-between gap-4">
          <div className="flex-1 min-w-0">
            <p className="text-[11px] uppercase tracking-[0.32em] text-text-secondary/60">{detail.owner}</p>
            <h2 className="mt-1.5 text-xl font-semibold text-text-primary leading-tight lg:text-2xl">{detail.title}</h2>
            <p className="mt-2 text-sm leading-relaxed text-text-secondary">
              {t('sponsor')}: {detail.sponsor}. {detail.description}
            </p>
          </div>
          <button
            type="button"
            className="relative flex-shrink-0 overflow-hidden rounded-full px-4 py-2 text-xs font-semibold uppercase tracking-[0.24em] text-base shadow-neon transition duration-200 ease-soft-spring hover:shadow-neon-hover whitespace-nowrap"
          >
            <span className="absolute inset-0 bg-[linear-gradient(135deg,rgba(0,255,136,0.85),rgba(64,224,208,0.85))]" />
            <span className="relative flex items-center gap-2">
              <Sparkles className="h-4 w-4" />
              {t('governanceBrief')}
            </span>
          </button>
        </div>
      </header>

      <div className="grid gap-3 sm:grid-cols-2">
        {metrics.map((metric) => (
          <div
            key={metric.label}
            className="rounded-2xl border border-border/60 bg-surface-muted/70 px-4 py-3 transition duration-200 ease-soft-spring hover:border-neon-green/60"
          >
            <p className="text-[11px] uppercase tracking-[0.28em] text-text-secondary/60">{metric.label}</p>
            <p className="mt-2 text-lg font-semibold text-text-primary">{metric.value}</p>
          </div>
        ))}
      </div>

      <div className="rounded-2xl border border-border/60 bg-surface-muted/70 px-4 py-4">
        <div className="flex items-center justify-between text-[11px] uppercase tracking-[0.28em] text-text-secondary/60">
          <span className="flex items-center gap-2">
            <CalendarClock className="h-4 w-4 text-neon-green" />
            {t('nextMilestone')}
          </span>
          <span>{detail.nextMilestone.due}</span>
        </div>
        <p className="mt-3 text-sm font-semibold text-text-primary">{detail.nextMilestone.label}</p>
        <p className="mt-2 text-xs leading-relaxed text-text-secondary">
          {t('nextMilestoneDescription')}
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        <div className="rounded-2xl border border-border/60 bg-surface-muted/70 px-4 py-4">
          <h3 className="text-sm font-semibold text-text-primary">{t('linkedExperiments')}</h3>
          <ul className="mt-3 space-y-2">
            {detail.linkedExperiments.map((exp) => (
              <li
                key={exp.id}
                className="flex items-center justify-between rounded-xl border border-border/60 bg-surface-hover/70 px-3 py-2 text-sm text-text-secondary"
              >
                <span className="text-text-primary">
                  {exp.id} · {exp.title}
                </span>
                <span className="rounded-full border border-border/60 px-2 py-0.5 text-[11px] uppercase tracking-wide text-text-secondary whitespace-nowrap">
                  {exp.status}
                </span>
              </li>
            ))}
          </ul>
        </div>
        <div className="rounded-2xl border border-border/60 bg-surface-muted/70 px-4 py-4">
          <h3 className="text-sm font-semibold text-text-primary">{t('gatingChecklist')}</h3>
          <ul className="mt-3 space-y-2">
            {detail.gatingChecklist.map((item) => (
              <li
                key={item.label}
                className={`flex items-center justify-between rounded-xl border px-3 py-2 text-sm transition duration-200 ease-soft-spring ${checklistTone[item.status]}`}
              >
                <span className="flex items-center gap-2 min-w-0">
                  <ShieldCheck className="h-4 w-4 flex-shrink-0" />
                  <span className="truncate">{item.label}</span>
                </span>
                {item.status === "complete" ? (
                  <CheckCircle2 className="h-4 w-4 flex-shrink-0 text-neon-green" />
                ) : (
                  <ExternalLink className="h-4 w-4 flex-shrink-0 text-text-secondary" />
                )}
              </li>
            ))}
          </ul>
        </div>
      </div>

      <div className="rounded-2xl border border-border/60 bg-surface-muted/70 px-4 py-4">
        <h3 className="text-sm font-semibold text-text-primary">{t('dependencies')}</h3>
        <ul className="mt-3 space-y-2 text-sm text-text-secondary">
          {detail.dependencies.map((dependency) => (
            <li key={dependency} className="flex items-center gap-3">
              <span className="h-1 w-6 rounded-full bg-gradient-to-r from-[rgba(0,255,136,0.7)] to-[rgba(64,224,208,0.7)] flex-shrink-0" />
              <span>{dependency}</span>
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
}

